<template>
  <v-container>
    <v-data-table
      v-model="selected"
      :headers="headers"
      :items="items"
      :search="search"
      :footer-props="{ itemsPerPageOptions: [10, 30, 50] }"
      show-select
      item-key="ID"
    >
      <template v-slot:top>
        <v-toolbar flat>
          <v-toolbar-title class="d-none d-sm-flex font-weight-bold text-sm-h5">
            Lançamentos
          </v-toolbar-title>
          <v-btn
            color="blue"
            class="ms-sm-5 ms-0"
            :dark="!disableBatchEdit"
            :disabled="disableBatchEdit"
            small
            @click="batchDialog = true"
          >
            Editar Lote
          </v-btn>

          <v-spacer />

          <v-text-field
            v-model="search"
            color="blue"
            append-icon="mdi-magnify"
            label="Procurar"
            hide-details
          />

          <v-spacer />

          <v-dialog
            v-model="batchDialog"
            max-width="50vw"
          >
            <v-card id="item card">
              <v-form
                id="sheetdb-form"
                ref="form"
                v-model="valid"
                class="form"
                method="POST"
                action="https://sheetdb.io/api/v1/6cm8q1svopqoj"
              >
                <v-card-title>
                  <span class="text-h4 text-md-h3">
                    IDs do Lote - 
                  </span>
                  <span
                    v-for="item in selected" 
                    v-bind:key="item.id"
                  >
                    &nbsp; {{ item.ID }}. &nbsp;
                  </span>
                </v-card-title>

                <v-card-text>
                  <v-container>
                    <v-row class="mb-n10">
                      <v-col
                        cols="12"
                        md="6"
                      >
                        <v-text-field
                          v-model="editedItem.VALORCOBRADO"
                          outlined
                          type="number"
                          min="0"
                          step="1"
                          color="blue"
                          prepend-icon="mdi-cash"
                          name="data[VALORCOBRADO]"
                          label="Valor Cobrado"
                        />
                        <v-text-field
                          v-model="editedItem.NUMBOLETO"
                          type="number"
                          name="data[NUMBOLETO]"
                          label="Nº Boleto"
                          prepend-icon="mdi-numeric"
                          color="blue"
                          outlined
                        />
                        <v-autocomplete
                          v-model="editedItem.STATUS"
                          color="blue"
                          name="data[STATUS]"
                          label="Status Financeiro"
                          :items="profile === 'ADMIN' ? statusesAdmin : statuses"
                          :prepend-icon="getIcon(editedItem.STATUS)"
                          auto-select-first
                          hide-details
                          outlined
                        />
                      </v-col>

                      <v-col
                        cols="12"
                        md="6"
                      >
                        <v-text-field
                          v-model="editedItem.VALORBOLETO"
                          type="number"
                          min="0"
                          step="1"
                          name="data[VALORBOLETO]"
                          label="Valor Boleto"
                          color="blue"
                          prepend-icon="mdi-cash-multiple"
                          outlined
                        />
                        <v-menu
                          offset-y
                          min-width="auto"
                        >
                          <template v-slot:activator="{ on, attrs }">
                            <v-text-field
                              v-model="editedItem.VENCIMENTOBOLETO"
                              v-mask="dateMask"
                              color="blue"
                              name="data[VENCIMENTOBOLETO]"
                              label="Vencimento Boleto"
                              outlined
                              @keydown="dMask(editedItem.VENCIMENTOBOLETO)"
                              @blur="data = parseDate(editedItem.VENCIMENTOBOLETO)"
                            >
                              <template v-slot:prepend>
                                <v-icon
                                  color="blue darken-3"
                                  v-bind="attrs"
                                  v-on="on"
                                >
                                  mdi-calendar-clock
                                </v-icon>
                              </template>
                            </v-text-field>
                          </template>
                          <v-date-picker
                            v-model="tempVencimentoBoleto"
                            no-title
                            show-adjacent-months
                            locale="pt-BR"
                          />
                        </v-menu>
                        <v-menu
                          offset-y
                          min-width="auto"
                        >
                          <template v-slot:activator="{ on, attrs }">
                            <v-text-field
                              v-model="editedItem.PAGAMENTOBOLETO"
                              :rules="rulesPAGAMENTOBOLETO"
                              v-mask="dateMask"
                              color="blue"
                              name="data[PAGAMENTOBOLETO]"
                              label="Pagamento Boleto"
                              outlined
                              required
                              @keydown="dMask(editedItem.PAGAMENTOBOLETO)"
                              @blur="data = parseDate(editedItem.PAGAMENTOBOLETO)"
                            >
                              <template v-slot:prepend>
                                <v-icon
                                  color="blue darken-3"
                                  v-bind="attrs"
                                  v-on="on"
                                >
                                  mdi-calendar-check
                                </v-icon>
                              </template>
                            </v-text-field>
                          </template>
                          <v-date-picker
                            v-model="tempPagamentoBoleto"
                            no-title
                            show-adjacent-months
                            locale="pt-BR"
                          />
                        </v-menu>
                      </v-col>
                    </v-row>
                    <v-row class="mb-n10">
                      <v-col cols="12">
                      <v-textarea
                          v-model="editedItem.OBSERVAÇÃO"
                          name="data[OBSERVAÇÃO]"
                          label="Observações"
                          color="blue"
                          rows="1"
                          row-height="15"
                          prepend-icon="mdi-message-text"
                          hide-details
                          outlined
                        />
                      </v-col>
                    </v-row>
                  </v-container>
                </v-card-text>

                <v-card-actions class="justify-end">
                  <v-btn
                    color="red darken-2"
                    text
                    @click="closeBatch"
                  >
                    Cancelar
                  </v-btn>
                  <v-btn
                    color="blue darken-3"
                    text
                    :disabled="!valid"
                    @click="saveBatch"
                  >
                    Salvar Lote
                  </v-btn>
                </v-card-actions>

              </v-form>
            </v-card>
          </v-dialog>
        </v-toolbar>
      </template>
    </v-data-table>
  </v-container>
</template>

<script>
  import axios from 'axios'

  export default {
    name: 'BatchView',

    data: () => ({
      valid: true,
      user: window.localStorage.user,
      profile: window.localStorage.profile,
      batchDialog: '',
      dateMask: '',
      search: '',
      timeNow: '',
      editedIndex: -1,
      tempVencimentoBoleto: '',
      tempPagamentoBoleto: '',
      objConditionals: {},
      batchItems: [],
      items: [],
      selected: [],
      statuses: [
        { value: 'A VENCER', text: 'A Vencer' },
        { value: 'TAXA PAGA', text: 'Taxa Paga' },
        { value: 'CONFERIR', text: 'Conferir' },
        { value: 'PAGO', text: 'Pago', disabled: true },
        { value: 'A COBRAR', text: 'A Cobrar' },
        { value: 'VENCIDO', text: 'Vencido' },
        { value: 'NÃO COBRAR', text: 'Não Cobrar' },
        { value: 'A DEPOSITAR', text: 'A Depositar' },
        { value: 'ACERTO', text: 'Acerto' },
      ],
      aetStatuses: [
        { value: 'RENOVADA', text: 'Renovada' },
        { value: 'PRE-PEDIDO', text: 'Pré Pedido' },
        { value: 'VALIDA', text: 'Válida', disabled: true },
        { value: 'A VENCER', text: 'A Vencer', disabled: true },
        { value: 'VENCIDA', text: 'Vencida', disabled: true },
      ],
      statusesAdmin: [
        { value: 'A VENCER', text: 'A Vencer' },
        { value: 'TAXA PAGA', text: 'Taxa Paga' },
        { value: 'CONFERIR', text: 'Conferir' },
        { value: 'PAGO', text: 'Pago' },
        { value: 'A COBRAR', text: 'A Cobrar' },
        { value: 'VENCIDO', text: 'Vencido' },
        { value: 'NÃO COBRAR', text: 'Não Cobrar' },
        { value: 'A DEPOSITAR', text: 'A Depositar' },
        { value: 'ACERTO', text: 'Acerto' },
      ],
      headers: [
        { text: 'ID', value: 'ID' },
        { text: 'Status', value: 'STATUS' },
        { text: 'Valor Cobrado', value: 'VALORCOBRADO' },
        { text: 'Vencimento Boleto', value: 'VENCIMENTOBOLETO' },
        { text: 'Pagamento Boleto', value: 'PAGAMENTOBOLETO' },
        { text: 'Nº Boleto', value: 'NUMBOLETO' },
        { text: 'Valor Boleto', value: 'VALORBOLETO' },
        { text: 'Observação', value: 'OBSERVAÇÃO' },
        { text: 'Edição', value: 'EDICAO', sortable: false, align: ' d-none'},
      ],
      editedItem: {
        ID: '',
        STATUS: '',
        VALORCOBRADO: '',
        VENCIMENTOBOLETO: '',
        PAGAMENTOBOLETO: '',
        VALORBOLETO: '',
        NUMBOLETO: '',
        OBSERVAÇÃO: '',
        EDICAO: '',
      },
      defaultItem: {
        ID: '',
        STATUS: '',
        VALORCOBRADO: '',
        VENCIMENTOBOLETO: '',
        PAGAMENTOBOLETO: '',
        VALORBOLETO: '',
        NUMBOLETO: '',
        OBSERVAÇÃO: '',
        EDICAO: '',
      },
    }),

    computed: {
      disableBatchEdit () {
        return this.selected.length > 1 ? false : true
      },
      rulesPAGAMENTOBOLETO () {
        const rules = []

        const rule =
          !this.editedItem.PAGAMENTOBOLETO ||
          this.editedItem.PAGAMENTOBOLETO.length === 10 ||
          (this.editedItem.STATUS !== 'PAGO' && this.editedItem.PAGAMENTOBOLETO.length === 10) ||
          'Campo Obrigatório'
        rules.push(rule)

        return rules
      },
    },

    watch: {
      batchDialog (val) {
        val || this.closeBatch()
      },
      tempVencimentoBoleto () {
        this.editedItem.VENCIMENTOBOLETO = this.formatDate(
          this.tempVencimentoBoleto,
        )
      },
      tempPagamentoBoleto () {
        this.editedItem.PAGAMENTOBOLETO = this.formatDate(
          this.tempPagamentoBoleto,
        )
      },
    },

    created () {
      // this.populateEditedItems()
      this.fetchItems()
    },

    methods: {
      fetchItems () {
        return fetch('https://sheetdb.io/api/v1/6cm8q1svopqoj')
          .then((r) => r.json())
          .then((r) => {
            this.items = r
          })
      },
      // populateEditedItems () {
      //   this.editedItem = this.items
      // },
      batchItemsUpdateConditionals () {
        let valorCobradoConditional = { VALORCOBRADO : `${this.editedItem.VALORCOBRADO}` }
        let numBoletoConditional = { NUMBOLETO : `${this.editedItem.NUMBOLETO}` }
        let statusConditional = { STATUS : `${this.editedItem.STATUS}` }
        let valorBoletoConditional = { VALORBOLETO : `${this.editedItem.VALORBOLETO}` }
        let vencimentoBoletoConditional = { VENCIMENTOBOLETO : `${this.editedItem.VENCIMENTOBOLETO}` }
        let pagamentoBoletoConditional = { PAGAMENTOBOLETO : `${this.editedItem.PAGAMENTOBOLETO}` }
        let obsConditional = { OBSERVA\u00c7\u00c3O : `${this.editedItem.OBSERVAÇÃO}` }

        if (this.editedItem.VALORCOBRADO) Object.assign(this.objConditionals, valorCobradoConditional)
        if (this.editedItem.NUMBOLETO) Object.assign(this.objConditionals, numBoletoConditional)
        if (this.editedItem.STATUS) Object.assign(this.objConditionals, statusConditional)
        if (this.editedItem.VALORBOLETO) Object.assign(this.objConditionals, valorBoletoConditional)
        if (this.editedItem.VENCIMENTOBOLETO) Object.assign(this.objConditionals, vencimentoBoletoConditional)
        if (this.editedItem.PAGAMENTOBOLETO) Object.assign(this.objConditionals, pagamentoBoletoConditional)
        if (this.editedItem.OBSERVAÇÃO) Object.assign(this.objConditionals, obsConditional)
      },
      updateBatchItems () {
        this.batchItemsUpdateConditionals()
        setTimeout(() => {
          let batchItemsIds = []
          let obj = {}

          batchItemsIds = this.selected.map(item => item.ID)
          for (let i = 0; i < this.selected.length; i++) {
            obj = { query:`ID=${batchItemsIds[i]}` }

            Object.assign(obj, this.objConditionals)

            this.batchItems.push(obj)
          }
        }, 200)
      },
      saveBatch () {
        this.updateBatchItems()
        setTimeout(() => {
          this.setEditTime()
          setTimeout(() => {
            axios.put(`https://sheetdb.io/api/v1/6cm8q1svopqoj/batch_update`, {
              data: this.batchItems // salva as alterações
            })
              .then((response) => {
                if (response.status === 200 || response.status === 201) {
                  window.alert('É TEEEEEEETRAAAAAAAA')
                  this.fetchItems()
                } else {
                  window.alert('não foi hj campeão')
                }
              })
            this.dialogSave = false
            setTimeout(() => {
              this.closeBatch()
            }, 200)
          }, 200)
        })
      },
      closeBatch () {
        this.batchDialog = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },
      validateField () {
        this.$refs.form.validate()
      },
      dMask (value) {
        console.log(value)
        const day = [
          /[0-3]/,
          /[0-9]/,
          // value.toString().charAt(0) != '3' ? /[0-9]/ : /[0-1]/,
        ]
        const month = [
          /[0-1]/,
          /[0-9]/,
          // value.toString().charAt(3) === '1' ? /[0-2]/ : /[0-9]/,
        ]
        const year = [
          /[2]/,
          /[0]/,
          /[1-2]/,
          /[0-9]/,
        ]
        this.dateMask = [...day, '/', ...month, '/', ...year]
      },
      formatDate (tempDate) {
        if (!tempDate) {
          return null
        }

        const [year, month, day] = tempDate.split('-')
        return `${day}/${month}/${year}`
      },
      parseDate (tempDate) {
        if (!tempDate || tempDate.length != 10) {
          return ''
        }

        const [day, month, year] = tempDate.split('/')
        return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
      },
      setEditTime () {
        const timeElapsed = Date.now()
        const rightNow = new Date(timeElapsed)
        this.timeNow = rightNow.toLocaleString('pt-BR')
      },
      getIcon (STATUS) {
        if (STATUS === 'PAGO') {
          return 'mdi-check-all blue--text'
        } else if (STATUS === 'A VENCER' || STATUS === 'A DEPOSITAR') {
          return 'mdi-cash-clock'
        } else if (STATUS === 'TAXA PAGA') {
          return 'mdi-check'
        } else if (STATUS === 'A COBRAR') {
          return 'mdi-clock-alert-outline'
        } else if (STATUS === 'CONFERIR') {
          return 'mdi-email-alert-outline'
        } else if (STATUS === 'VENCIDO') {
          return 'mdi-alert red--text'
        } else if (STATUS === 'NÃO COBRAR' || STATUS === 'ACERTO') {
          return 'mdi-cash-remove red--text'
        } else { return 'mdi-list-status' }
      },
      getIcon2 (STATUSAET) {
        if (STATUSAET === 'VALIDA') {
          return 'mdi-check-all green--text'
        } else if (STATUSAET === 'A VENCER') {
          return 'mdi-alert'
        } else if (STATUSAET === 'VENCIDA') {
          return 'mdi-clock-alert'
        } else if (STATUSAET === 'RENOVADA') {
          return 'mdi-check'
        } else { return 'mdi-list-status' }
      },
    },
  }
</script>
